import * as React from 'react';
import {reduxForm, InjectedFormProps, Field} from 'redux-form';

import ColorGroup from '../../FormElements/ColorGroup';


const AddCartForm: React.FC<InjectedFormProps<{}>> = (props) => (
	<form onSubmit={props.handleSubmit}>
		<div className="row space-between product__facilities">
			<div className="product__size row">
				<p>Size:</p>
				<ul className="product__size-list">
					<li className="product__size-item">XS</li>
					<li className="product__size-item">S</li>
					<li className="product__size-item product__size-item_active">M</li>
					<li className="product__size-item">L</li>
				</ul>
			</div>

			<div className="product__color row">
				<p>Color:</p>

				<Field
					component={ColorGroup}
					name="color"
					colors={['red', 'green', 'blue']}
				/>
			</div>
		</div>

		<div className="my-pad">
			<div className="order">
				<div className="order__quantity">
					<b className="order__quantity-head">Quantity:</b>

					<span
						className="order__quantity-count"
						contentEditable>1</span>
				</div>

				<div className="order__actions row mt-2">
					<div className="order__like">
						<i className="fas fa-heart"/>
					</div>

					<div className="order__add">Add to cart</div>
				</div>
			</div>
		</div>
	</form>
);

export default reduxForm({
	form: 'addCart'
})(AddCartForm);
